*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}
body {
  background-image: -webkit-radial-gradient( rgba(30,145,214,1),rgba(88,173,221,1));
  background-image: radial-gradient( rgba(30,145,214,1),rgba(88,173,221,1));
  color: #000;
  font-family: 'Lora', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.wrapper {
    height: 100vh;
    /* background-color: bisque; */
    position: relative;
}
.textContainer{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    font-size: 4.5rem;
    opacity: 0;
    -webkit-animation: showText 2s forwards ease-in;
            animation: showText 2s forwards ease-in;
}

@media only screen and (max-width: 600px) {
    .textContainer{
        font-size: 3rem;
    }
}
.line1 {
    width: 1750px;
    height: 33.33333333%;
    -webkit-animation: inFromRight 3s linear forwards;
            animation: inFromRight 3s linear forwards;
    -webkit-transform: translateX(100%), rotate(45deg);
            transform: translateX(100%), rotate(45deg);
    object-fit: fill;
}

.line2 {
    width: 1750px;
    height: 33.33333333%;
    /* animation: inFromLeft 3.7s linear forwards; */
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    object-fit: fill;
    opacity: 0;
    
}

.line3 {
    width: 1750px;
    height: 33.33333333%;
    -webkit-animation: inFromLeft 3s linear forwards;
            animation: inFromLeft 3s linear forwards;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

.flag {
    border: 2px solid transparent;
    display: inline-block;
    height: 100%;
    width: 350px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 10px;
    /* opacity: .7; */
    /* filter: grayscale(50%); */
}

@-webkit-keyframes showText{
    0% {
        opacity: 0;
    }
    20% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

@keyframes showText{
    0% {
        opacity: 0;
    }
    20% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes inFromRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    25% {
        opacity: 1;
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
    50% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
        opacity: 1;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
    85% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}

@keyframes inFromRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    25% {
        opacity: 1;
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
    50% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
        opacity: 1;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
    85% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}

@-webkit-keyframes inFromLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    25% {
        opacity: 1;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
    50% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
        opacity: 1;
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
    85% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}

@keyframes inFromLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    25% {
        opacity: 1;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
    50% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
        opacity: 1;
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
    85% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}
