.navigation {
  display: block;
}
.navigation__checkbox {
  background-color: #fff;
  height: 4.8rem;
  width: 4.8rem;
  position: fixed;
  top: 2rem;
  right: 3rem;
  border-radius: 50%;
  z-index: 2000;
  box-shadow: 0 1rem 3rem rgba(#000, .1);
  text-align: center;
  cursor: pointer;
}

.navigation__button {
  cursor: pointer;
}

.navigation__background {
  height: 4.8rem;
  width: 4.8rem;
  border-radius: 50%;
  position: fixed;
  top: 2rem;
  right: 3rem;
  /* background-image: radial-gradient(rgba(126,213,111,1), rgba(40,180,133,1)); */
  background-image: -webkit-radial-gradient( rgba(30,145,214,1),rgba(88,173,221,1));
  background-image: radial-gradient( rgba(30,145,214,1),rgba(88,173,221,1));
  z-index: 1000;
  -webkit-transition: -webkit-transform .8s cubic-bezier(0.86, 0, 0.07, 1);
  transition: -webkit-transform .8s cubic-bezier(0.86, 0, 0.07, 1);
  transition: transform .8s cubic-bezier(0.86, 0, 0.07, 1);
  transition: transform .8s cubic-bezier(0.86, 0, 0.07, 1), -webkit-transform .8s cubic-bezier(0.86, 0, 0.07, 1);
}

.navigation__nav {
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;

  opacity: 0;
  width: 0;
  -webkit-transition: all .8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: all .8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.navigation__list {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  padding: 0 2rem;
  text-align: center;
  width: 100%;
  font-size: 2rem;
}

.navigation__item {
  margin: 1rem;
}

.instructions {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
}

.navigation__link:link,
.navigation__link:visited  {
  display: inline-block;
  font-size: 3rem;
  font-weight: 300;
  padding: 1rem 2rem;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  background-image: -webkit-linear-gradient(330deg, transparent 0%, transparent 50%, rgba(0, 0, 0, 1) 50%);
  background-image: linear-gradient(120deg, transparent 0%, transparent 50%, rgba(0, 0, 0, 1) 50%);
  background-size: 220%;
  -webkit-transition: all .4s;
  transition: all .4s;
}

.navigation__link span {
  margin-right: 1.5rem;
  display: inline-block;
}

.navigation__link:hover,
.navigation__link:active {
  background-position: 100%;
  color: #55c57a;
  -webkit-transform: translateX(1rem);
          transform: translateX(1rem);
}

.navigation__checkbox:checked ~ .navigation__background {
  -webkit-transform: scale(80);
          transform: scale(80);
}

.navigation__checkbox:checked ~ .navigation__nav {
  opacity: 1;
  width: 100%;
}

.navigation__icon {
  position: fixed;
  top: .8rem;
  right: 3.9rem;
  margin-top: 3.5rem;
  width: 3rem;
  height: 2px;
  background-color: #333;
  display: inline-block;
  z-index: 50000;
}

.navigation__icon::before,
.navigation__icon::after {
  width: 3rem;
  height: 2px;
  background-color: #333;
  display: inline-block;
}


.navigation__icon::before,
.navigation__icon::after {
  content: "";
  position: absolute;
  left: 0;
  -webkit-transition: all .2s;
  transition: all .2s;
}

.navigation__icon::before { top: -.8rem; }
.navigation__icon::after { top: .8rem; }

.navigation__button:hover .navigation__icon::before {
  top: -1rem;
}

.navigation__button:hover .navigation__icon::after {
  top: 1rem;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: transparent;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::before {
  top: 0;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::after {
  top: 0;
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
}

